import { Component, OnInit } from '@angular/core';
import { VendorLayoutService } from '../../vendor-layout.service';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  user: any;

  defaultPasswords = {
    password: '',
    new_password: '',
    confirm_password: ''
  };

  passwords = { ...this.defaultPasswords };

  constructor(
    private userService: UserService,
    private vendorLayoutService: VendorLayoutService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
    this.vendorLayoutService.setPageTitle('Profile');
    const current_user = JSON.parse(localStorage.getItem('user'));
    this.userService.getVendor(current_user.vendor.id).subscribe(
      data => this.user = data
    );
  }

  updatePassword() {
    this.userService.updateVendorPassword(this.user.data.id, this.passwords).subscribe(
      data => {
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          this.auth();
        } else {
          this.toastrService.error(data.message);
        }
        this.passwords = { ...this.defaultPasswords };
      }
    );
  }

  updateData() {
    this.userService.updateVendor(this.user.data.id, this.user.data).subscribe(
      data => {
        if (data.status === 'success') {
          this.user = data;
          this.toastrService.success(data.message);
          this.auth();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

  uploadImageInput(event: any) {
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        const uploadData = {
            type: ext,
            img
        };
        this.userService.updateVendorAvatar(this.user.data.id, uploadData)
        .subscribe(data => {
          if (data.status === 'success') {
            this.toastrService.success(data.message);
            this.user = data;
            this.auth();
          } else {
            this.toastrService.error(data.message);
          }
        });
    };
    reader.readAsDataURL(file);
  }

  auth() {
    this.userService.auth().subscribe(
      data => {
      if (data.status === 'success') {
        this.userService.setUser(data.data);
      }
    });
  }



}
