import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { VendorLayoutService } from '../../vendor-layout.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-pins',
  templateUrl: './pins.component.html',
  styleUrls: ['./pins.component.css']
})
export class PinsComponent implements OnInit {

  pins: any;

  visiblePins = [];


  constructor(
    private userService: UserService,
    private vendorLayoutService: VendorLayoutService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
    this.vendorLayoutService.setPageTitle('User Pins');
    this.getPins();
  }


  getPins() {
    this.userService.getVendorPins(this.userService.current.value.id).subscribe(
      data => {
        this.pins = data;
        console.log(data);
      }
    );
  }

  hidePin(pin: string, length: number) {
    if (!this.visiblePins.includes(pin)) {
      let result = '';
      for (let x = 0; x < pin.length; x++) {
        result += x < length || x >= pin.length - length || pin[x] === '-' ? pin[x] : 'x';
      }
      return result;
    }
    return pin;
  }

  togglePinVisible(pin) {
    if (this.visiblePins.includes(pin)) {
      this.visiblePins.splice(this.visiblePins.indexOf(pin), 1);
    } else {
      this.visiblePins.push(pin);
    }
  }



}
